.interior__container{
  display: flex;

}

.interior__list{

}

.aaccordion__control {
	width: 100%;
	background-color: transparent;
	cursor: pointer;
  position: relative;
  padding-right: 20px;
  padding-left: 44px;
  text-align: left;

  &:focus {
    outline: 2px solid var(--color-primary);
    outline-offset: 2px;
}
}

.aaccordion__content {
	max-height: 0;
	opacity: 0;
	overflow: hidden;
	padding: 0 20px;
	will-change: max-height;
	transition: all 0.3s ease-out;
	box-sizing: content-box;
}

.aaccordion__title {
	font-weight: 700;
  font-size: 28px;
  line-height: 150%;
  color: var(--color-ebony);
}

.aaccordion__icon {
  position: absolute;
  left: 1px;
  top: 10px;
  display: block;
  width: 22px;
  height: 22px;
	transition: transform 0.3s ease-out;
}

.aaccordion__icon::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  border-radius: 100px;
  width: 100%;
  height: 2px;
  background-color: var(--color-primary);
  transform: translateY(-50%);
}

.aaccordion__icon::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  border-radius: 100px;
  width: 2px;
  height: 100%;
  background-color: var(--color-primary);
  transform: translateX(-50%);
  transition: opacity 0.3s ease-in-out;

}



.open .aaccordion__icon {
	transform: rotate(90deg);
}

.open .aaccordion__content {
	opacity: 1;
	padding: 20px;
}

.open .aaccordion__icon::before {
	opacity: 0;
  transition: opacity 0.3s ease-in-out;
}
